@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;1,200;1,300;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
@-webkit-keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

:root{
  --pageColor:#eceef1;
}
body{
  background-color: #eceef1;
}
*{
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}
a{
  text-decoration: none;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}
.box-element {
  box-shadow: hsl(0, 0%, 80%) 0 0 16px;
  background-color: #fff;
  border-radius: 4px;
  padding: 10px;
}

/* Main GRID */
#App-grid{
  display: grid;
  grid-template-columns:1fr;
  grid-template-rows: minmax(67px,auto) minmax(calc(90vh - 70px),auto) 1fr;
  grid-template-areas:
  "header" "content" "footer";
}
#header{
  grid-area: header;
}

#footer{
  grid-area: footer;
  height: 20vh;
  background-image: linear-gradient(60deg, #29323c 0%, #485563 100%);}
#content{
  min-height: 80vh;
  grid-area: content;
}

.any-container{
  padding:0.3rem;
}

@media screen and (min-width:720px){
  .any-container{
    padding: 1rem 3%;
  }
}
@media screen and (min-width:960px){
  .any-container{
    padding: 1rem 4%;
  }
}

@media screen and (min-width:1080px){
  .any-container{
    padding: 2rem 5%;
  }
}
h1,h2,h3,h4,h5,h6,p,p1{
