.arrow{
    transition: 350ms;
    cursor: pointer;
}

.arrow.right{
    right: -100%;
}
.arrow.left{
    left: -100%;
}

.arrow.right.active{
    right:0;
}
.arrow.left.active{
    left:0;
}

#trigger-element .arrow.active.detail#detail{
    z-index: 6;
}

.arrow.active.detail p{
    background-color: rgb(17, 85, 31);
}

.detail-container{
    position: fixed;
    background-image: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
    min-height: 400px;
    width:350px;
    border-radius: 10px;
    z-index: 5;
    transition: 500ms;
}
.detail-container.active{
    z-index: 5;
}


h1.title{
